import { Routes, RouterModule } from "@angular/router";
import { ModuleWithProviders } from "@angular/core";
import { EstablishRequirementsQueryComponent } from "./establish-requirements-query/establish-requirements-query.component";
import { EstablishRequirementsSaveComponent } from "./establish-requirements-save/establish-requirements-save.component";
import { EstablishRequirementsVerificationComponent } from "./establish-requirements-verification/establish-requirements-verification.component";
import { EstablishRequirementsDetailComponent } from "app/pages/tzb/custom/establish-requirements/establish-requirements-detail/establish-requirements-detail.component";

export const routes: Routes = [
    {
        path: '',
        children: [
            {path: 'establish-requirements-query',component: EstablishRequirementsQueryComponent},
            {path: 'establish-requirements-save',component: EstablishRequirementsSaveComponent},
            {path: 'establish-requirements-verification',component: EstablishRequirementsVerificationComponent},
            {path: 'establish-requirements-detail',component: EstablishRequirementsDetailComponent}
        ]
    }

]

export const EstablishRequirementsRouting: ModuleWithProviders = RouterModule.forChild(routes);
