import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { EstablishRVBean } from "./bean/establish-requirements-verification.bean";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
import * as _ from 'lodash';

@Component({
    selector: 'establishRequirementsVerification',
    templateUrl: './establish-requirements-verification.component.html',
    styleUrls: ['./establish-requirements-verification.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class EstablishRequirementsVerificationComponent implements OnInit {

    operate: string = '1';  // ->(query)'1'  (add)'2'  (update)'3'
    establishRVBean: EstablishRVBean = new EstablishRVBean();
    msgs: Message[] = [];
    first: number = 0;
    totalNum: number = 0;
    tableList: any = [];
    queryList: any = [];
    length: number;
    code: any;
    // 
    orderDataList: any = [];
    upNo: number;
    upOrSave: string = '3';
    sanyaosuadd2Next: any = [];
    siyaosuadd2Next: any = [];
    sanyaosuaddNext: any = [];
    siyaosuaddNext: any = [];
    updateOtherColtwo: any = [];
    updateOtherColthree: any = [];
    sanyaosuaddAdd2Next: any = [];
    sanyaosuaddAddNext: any = [];

    constructor(
        public commfunc: Commfunc,
        public httpService: CusOperationHttpService,
        public confirmationService: ConfirmationService,
    ) {
        //
        this.codeValues();
        this.orderDataList = this.code['orderDataList']
    }

    ngOnInit() {
        this.querySecle();
    }

    querySecle() {
        this.establishRVBean.pageSize = 10;
        this.establishRVBean.pageNum = 1;
        this.first = 0;
        this.query();
    }

    // (1)
    query() {
        if (this.establishRVBean.verifyPhase && this.establishRVBean.verifyPhase == '') {
            this.establishRVBean.verifyPhase = undefined;
        }
        let ob = this.httpService.findVerifyConfigList(this.establishRVBean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.queryList = data.verifyRelList == null ? [] : data.verifyRelList;
                this.totalNum = data.totalNum;
                this.operate = '1';
                this.tableList = []
                if (this.queryList.length > 0) {
                    this.queryList.forEach((element, index) => {
                        let sr = {}
                        sr['effectiveDays'] = element.effectiveDays
                        sr['seqNo'] = element.seqNo
                        sr['verifyPhase'] = element.verifyPhase
                        if (element.channelNo.indexOf(',') != -1) {
                            sr['channelNo'] = element.channelNo.split(',')
                        } else {
                            let a = [];
                            a.push(element.channelNo)
                            sr['channelNo'] = a
                        }
                        this.tableList.push(sr)
                    });
                } else {
                    this.tableList = this.queryList;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
    // 
    paginate(event) {
        this.establishRVBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.establishRVBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.establishRVBean.pageSize;
        this.query();
    }
    // 
    toDelete(items, i) {
        if (items.seqNo == '') {
            this.tableList.splice(i, 1);
            this.operate = '1'
        } else {
            this.confirmationService.confirm({
                message: '?',
                accept: () => {
                    let param = {
                        seqNo: items.seqNo,
                        operationType: '03',
                    }
                    let ob3 = this.httpService.verifyElement(param);
                    ob3.subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            this.operate = '1'
                            this.msgs = [];
                            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                            this.querySecle();
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: "" });
                    })
                }
            });
        }
    }
    // -'2'
    doAdd() {
        // 
        let bool = true
        this.tableList.forEach(element => {
            if (element.flag && element.flag == '1') {
                bool = false
            }
        });
        if (!bool) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        // 
        this.operate = '2'
        let arr = {
            seqNo: '',
            verifyPhase: '',
            effectiveDays: '0',
            channelNo: [''],
            flag: '1'
        }
        this.tableList.push(arr)
    }
    // -
    toAdd(item) {
        if (item.seqNo == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        } else {
            var regAmt = /^[0-9]*$/;
            if (!regAmt.test(item.seqNo)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return
            }
        }
        if (item.verifyPhase == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        if (item.effectiveDays == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        } else {
            var regAmt = /^[0-9]*$/;
            if (!regAmt.test(item.effectiveDays)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return
            }
        }
        let bool = true;
        let mm;
        item.channelNo.forEach((element, index) => {
            if (bool) {
                if (element == undefined || element == '') {
                    bool = false;
                    mm = index + 1;
                }
            }
        });
        if (!bool) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        let param = _.clone(item);
        // -
        if (param.flag) {
            delete param.flag
            // delete param.seqNo
        }
        // -'01'
        param['operationType'] = '01';
        let ss = '';
        let s1 = '';
        param.channelNo.forEach(item => {
            ss += item + ','
        });
        if (ss != '') {
            if (ss.charAt(ss.length - 1) == ',') {
                s1 = ss.substring(0, ss.length - 1)
            } else {
                s1 = ss
            }
        }
        if (param.channelNo) {
            delete param.channelNo
        }
        param['channelNo'] = s1;
        let ob2 = this.httpService.verifyElement(param);
        ob2.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.operate = '1'
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                this.querySecle();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })

    }

    // -'3'
    toEdit(item, i) {
        this.upNo = i;
        let param = _.clone(item);
        this.operate = '3';
        this.upOrSave = 'save';
    }

    // -
    toSave(item, i) {
        if (item.verifyPhase == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        if (item.effectiveDays == '') {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        } else {
            var regAmt = /^[0-9]*$/;
            if (!regAmt.test(item.effectiveDays)) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
                return
            }
        }
        let bool = true;
        let mm;
        item.channelNo.forEach((element, index) => {
            if (bool) {
                if (element == undefined || element == '') {
                    bool = false;
                    mm = index + 1;
                }
            }
        });
        if (!bool) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            return
        }
        let param = _.clone(item);
        param['operationType'] = '02';
        let ss = '';
        let s1 = '';
        param.channelNo.forEach(item => {
            ss += item + ','
        });
        if (ss != '') {
            if (ss.charAt(ss.length - 1) == ',') {
                s1 = ss.substring(0, ss.length - 1)
            } else {
                s1 = ss
            }
        }
        if (param.channelNo) {
            delete param.channelNo
        }
        param['channelNo'] = s1;
        let ob2 = this.httpService.verifyElement(param);
        ob2.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.operate = '1'
                this.upOrSave = '3';
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
                this.querySecle();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }
}