    toAdd(type) {
        let param = {
            clientNo: this.bean.custNo,
            appUserId: this.userId,
            toProcessingFlag: '0',
            appBranchNo: this.orgId,
        }
        this.creditCcmService.BZ_0200200002504_LOAN(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.handleFlag == '0' && type != 'maxAmt') {
                    this.isAddDisplay = true;
                    this.message = data.reason;
                } else {
                    if (this.bean.chanelType && (this.bean.chanelType == '2' || this.bean.chanelType == '1')) {
                        if (this.bean.tranType && this.bean.tranType != '04') {
                            this.prodName = undefined;
                            this.prodId = undefined;
                            this.bean.productNo = undefined;
                            this.bean.productName = undefined;
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '', detail: '' });
                            return
                        } else {
                            this.getProductDetail('add');
                        }
                    } else {
                        this.getProductDetail('add');
                    }

                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
