    // 
    getCustSource() {
        let querySource = {
            tranType: this.bean.tranType,
            custNo: this.bean.custNo,
            chanelType: '01', // 
        }
        this.httpService.selectCustSource(querySource).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.customerSourceOption = [];
                if (data.custSource != null && data.custSource != undefined && data.custSource != '') {
                    this.customerSource.forEach(element => {
                        if (element.value == data.custSource) {
                            this.customerSourceOption.push(element)
                        }
                    });
                    this.bean.customerSource = data.custSource;
                } else {
                    this.customerSourceOption = this.customerSourceDefault;
                }
            } else {
                this.customerSourceOption = this.customerSourceDefault;
            }
        }, error => {
            this.customerSourceOption = this.customerSourceDefault;
        })
    }

