import { Component, OnInit } from "@angular/core";
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { EstablishRequirementsDetailBean, SelectByPageBean } from "./bean/establish-requirements-detail.bean";
import { Message } from "primeng/primeng";
import { CALENDAR_CN } from "../../../../../@uisftech/common/constant/config";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { CusViewHttpService } from "app/pages/tzb/custom/http/custom-view/custom-view.http.service";
import * as _ from 'lodash';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";

@Component({
    selector: 'establishRequirementsDetail',
    templateUrl: './establish-requirements-detail.component.html',
    styleUrls: ['./establish-requirements-detail.component.scss'],
    providers: [Commfunc]
})
export class EstablishRequirementsDetailComponent implements OnInit {

    bean: EstablishRequirementsDetailBean = new EstablishRequirementsDetailBean();
    selectByPageBean: SelectByPageBean = new SelectByPageBean();
    msgs: Message[];
    ch: any = CALENDAR_CN;
    creditStatus: any = []; // 
    customerSource: any = [];//
    guaranteeMode: any = [];//
    tablelist: any = [];
    totalNum: number = 0;
    code: any;
    first: number = 0;
    theRequest: any = [];
    arrayList: any = [];
    arrayListDetailInfo: any = [];
    showBtn: boolean = true;
    associatesTypeListAll: any = []; // -
    creditReasonsListAll: any = []; // -

    zhengjianNum: any;//
    itemParam: any;//
    fxdjOptions: any = []//
    panduanOptions: any = [];//
    sortF: any;
    showReson: boolean = true;
    zxxxInfo: any = [];//
    custId:string;
    reportDisplay: boolean = false;
    progress: boolean = false;
    certType: string;
    certID: string;
    custNo: string;
    custName: string;
    custInfo: any;
    associatesExsit: boolean = false;

    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public router: Router,
    ) {
        //
        this.codeValues();
        this.associatesTypeListAll = this.code['associatesTypeListAll'];
        this.creditReasonsListAll = this.code['creditReasonsListAll']; // -
        this.creditStatus = this.code['creditStatusList'];
        this.customerSource = this.code['customerSource'];
        this.guaranteeMode = this.code['GuaranteeModeCustRequest'];

        // -
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
        }
        if (s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextNode
        }
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

        if (this.route.snapshot.params) {
            if (this.route.snapshot.params['taskId']) {
                let url = location.href;  //url
                if (url.indexOf(";") != -1) {
                    let str = url.substr(1);
                    let strs = str.split(";");
                    for (let i = 0; i < strs.length; i++) {
                        this.theRequest[strs[i].split("=")[0]] = (strs[i].split("=")[1]);
                    }
                }
                this.arrayList = JSON.parse(this.route.snapshot.params['dataMap']);
                this.bean.custRequestId = this.arrayList.custRequestId;
                this.selectByPageBean.custRequestId = this.arrayList.custRequestId;
                this.showBtn = false;
            } else {
                this.bean.custRequestId = this.route.snapshot.params.custRequestId;
                this.selectByPageBean.custRequestId = this.route.snapshot.params.custRequestId;
                this.showBtn = true;
            }
        }
        this.selectByPageBean.pageSize = 10;
        this.selectByPageBean.pageNum = 1;
        this.first = 0;
        this.queryBasicInfo();
        this.queryZxsqInfo();
    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {

    }

    // 
    queryBasicInfo() {

        // 
        let ob = this.httpService.getCustRequestInfo({ custRequestId: this.bean.custRequestId });
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                for (let item in data) {
                    if(item && item == 'loanAmt'){
                        let loanAmt = data[item];
                        if(loanAmt &&  loanAmt.toString().indexOf(",")==-1){                            
                            loanAmt = this.commfunc.formatMoney(loanAmt,2);
                        }
                        this.bean[item] = loanAmt;
                    } else {
                        this.bean[item] = data[item];
                    }                    
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }


    // 
    queryZxsqInfo() {
        // 
        let ob1 = this.httpService.selectCusstRequestAssociatesList(this.selectByPageBean);
        ob1.subscribe(data => {
            if (data.returnCode.code == 'success') {
                debugger
                this.tablelist = data.associatesList == null ? [] : data.associatesList;
                this.totalNum = data.totalNum;

                let bool = false;
                this.tablelist.forEach(element => {
                    
                    // 
                    if (element.associatesEnterName && element.associatesEnterName != '' && element.associatesEnterName != null && element.associatesEnterName != undefined
                        || element.associatesCreditCode && element.associatesCreditCode != '' && element.associatesCreditCode != null && element.associatesCreditCode != undefined) {
                        bool = true;
                    }
                });
                if (bool) {
                    this.associatesExsit = true;
                } else {
                    this.associatesExsit = false;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    // 
    doBack() {
        let paramNo = {
            custNo: this.bean.custNo,
        }
        this.router.navigate(['/pages/tzb/custom/establish-requirements/establish-requirements-query', paramNo]);
    }

    //
    paginate(event) {
        this.selectByPageBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.selectByPageBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.selectByPageBean.pageSize;
        this.queryZxsqInfo();
    }

     //
     queryZxxxInfo(col) {
        if (col.associatesEnterName) {//
            this.custInfo ={globalId: col.associatesCreditCode,clientType:'C',associatesCreditCode:col.associatesCreditCode};
            this.reportDisplay = true;
        }else{//
            this.custInfo = {globalId:col.globalId,clientType:'P'};
            this.reportDisplay = true;
        }
    }
}