/**
 * @author fanhualing 2018-8-31 
 */
import { Commfunc } from "../../../../../@uisftech/common/service/commonFun";
import { Component, OnInit } from "@angular/core";
import { EstablishRequirementsQueryBean } from "./bean/establish-requirements-query.bean";
import { Message } from "primeng/primeng";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CreditCcmService } from "app/pages/tzb/custom/credit-ccm/common/http/credit-ccm.http.service";
import { CommonHttpService } from 'app/pages/tzb/common/http/common.http.service';
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";
import { BussinessMap, AppproResultBean, SubApproveMapBean } from "app/pages/tzb/customCenter/customer-distribution/bean/business-information.bean";
import { CusViewHttpService } from "app/pages/tzb/custom/http/custom-view/custom-view.http.service";

@Component({
    selector: 'establishRequirementsQuery',
    templateUrl: './establish-requirements-query.component.html',
    styleUrls: ['./establish-requirements-query.component.scss'],
    providers: [CusOperationHttpService, Commfunc, ConfirmationService, CreditCcmService, CustomCenterHttpService]
})
export class EstablishRequirementsQueryComponent implements OnInit {

    bean: EstablishRequirementsQueryBean = new EstablishRequirementsQueryBean();
    msgs: Message[];
    tabMesg: any = tableMessage;//
    first: number = 0;
    totalNum: number = 0;
    statusList: any = [];//
    code: any;
    tablelist: any;
    selectProductDisplay: boolean = false;//
    prodName: string;
    isCatelog: boolean = true;
    associatesType: any = []; // 
    custAttribute: any = []; // 
    guaranteeMode: any = [];//
    display: boolean = false;

    vetoFirsts: any = [];//
    personVeto: any = [];
    financeVeto: any = [];
    manageVeto: any = [];
    serveVeto: any = [];
    otherVeto: any = [];
    vetoOption: any = [];

    firstVetoReason: string;
    secondVetoReason: string;

    user: any;
    user1: any;
    userId: string;
    userName: string;
    orgId: string;
    orgName: string;

    custRequestId: string;// 
    comments: string;
    bzIsshow: boolean = false;

    isCommitDis: boolean = true;

    VetoData: any = [];
    postNameVeto: string = '';
    bussinessMap: BussinessMap = new BussinessMap();
    appproResultBean: AppproResultBean = new AppproResultBean();
    subApproveMapBean: SubApproveMapBean = new SubApproveMapBean();
    reasonOne: string = ''; // 
    reasonTwo: string = ''; // 
    message: string;
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public route: ActivatedRoute,
        public confirmationService: ConfirmationService,
        public router: Router,
        private creditCcmService: CreditCcmService,
        public commonHttpService: CommonHttpService,
        public customCenterHttpService: CustomCenterHttpService,
        public httpViewService: CusViewHttpService,
    ) {
        //
        this.codeValues();
        this.custAttribute = this.code['CUST_ATTRIBUTES']
        this.associatesType = this.code['associatesTypeList'];
        this.statusList = this.code['EstablishRequireStatus'];
        this.guaranteeMode = this.code['GuaranteeModeCustRequest'];

        this.vetoFirsts = this.code['VETOFIRDTS'];//
        this.personVeto = this.code['personVeto'];//-
        this.financeVeto = this.code['financeVeto'];//
        this.manageVeto = this.code['manageVeto'];//
        this.serveVeto = this.code['serveVeto'];//
        this.otherVeto = this.code['otherVeto'];//

        // -
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));

        let postIdparam = s.extParam['postId'];

        if (s.extParam.specifyNextOpers) {
            delete s.extParam.specifyNextOpers
        }
        if (s.extParam.specifyNextNode) {
            delete s.extParam.specifyNextNode
        }
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

        if (this.route.snapshot.params) {
            this.bean.custNo = this.route.snapshot.params.custNo;
        }
        this.bean.custRequestTypeId = 'LOAN_REQUEST' //-;

        // 
        this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
        if (this.user1.userId) {
            this.userId = this.user1.userId;
        }
        if (this.user1.orgId) {
            this.orgId = this.user1.orgId;
        }
        if (this.user.userName) {
            this.userName = this.user.userName;
        }
        if (this.user.orgName) {
            this.orgName = this.user.orgName;
        }
        // custRequestIdSave;
        if (this.commfunc.getSessionDataCH('custRequestIdSave')) {
            this.commfunc.clearSession('custRequestIdSave');
        }

        let Postparam = {
            pageSize: '999999',
            postId: postIdparam,

        }
        this.commonHttpService.queryVaguePostList(Postparam).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.postList && data.postList.length > 0) {
                    this.postNameVeto = data.postList[0].postName;
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        })

    }

    // 
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        this.first = 0;
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.bean.statusId = '00';
        this.querySecle();
    }
    //
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }
    // 
    querySecle() {
        this.bean.pageSize = 10;
        this.bean.pageNum = 1;
        this.first = 0;
        this.doQuery();
    }

    // 
    doQuery() {
        // 
        if (this.bean.statusId == '' || this.bean.statusId == null) {
            this.bean.statusId = undefined;
        }
        // 
        if (this.bean.productNo == '') {
            this.bean.productNo = undefined;
        }
        if (this.bean.productName == '') {
            this.bean.productName = undefined;
        }
        // 
        let ob = this.httpService.findCustRequestInfoList(this.bean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tablelist = data.custRequestList == null ? [] : data.custRequestList;
                this.totalNum = data.totalNum;
                this.isCommitDis = true;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                this.isCommitDis = true;
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
            this.isCommitDis = true;
        })
    }

    // 
    paginate(event) {
        this.bean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.bean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.bean.pageSize;
        this.doQuery();
    }

    // 
    reset() {
        this.bean.productNo = undefined;
        this.bean.productName = undefined;
        this.bean.statusId = undefined;
        this.prodName = undefined;
    }
    // 
    choosePro() {
        this.selectProductDisplay = true;
    }

    // 
    closeSetting1() {
        this.selectProductDisplay = false;
    }

    // 
    productCall(event) {
        if (event && event.length > 0) {
            this.prodName = event[0].productName;
            this.bean.productNo = event[0].productId;
            this.bean.productName = event[0].productName;
        }
        this.selectProductDisplay = false;
    }

    //  
    toAdd() {
        if (this.commfunc.getSessionDataCH('lonkuan')) {
            this.commfunc.clearSession('lonkuan');
        }
        if (this.commfunc.getSessionDataCH('add')) {
            this.commfunc.clearSession('add');
        }
        let paramNo = {
            custNo: this.bean.custNo,
        }
        this.router.navigate(['/pages/tzb/custom/establish-requirements/establish-requirements-save', paramNo]);
    }

    // 
    toDetails(item) {
        let paramNo = {
            custNo: this.bean.custNo,
            custRequestId: item.custRequestId
        }
        this.router.navigate(['/pages/tzb/custom/establish-requirements/establish-requirements-detail', paramNo]);

    }

    // 
    toEdit(item) {
        let paramNo = {
            custNo: this.bean.custNo,
            custRequestId: item.custRequestId
        }
        this.router.navigate(['/pages/tzb/custom/establish-requirements/establish-requirements-save', paramNo]);
    }

    // 
    toCommit(item) {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader['globalSeqNo'] = '';
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        let paramGlob = {};
        this.httpService.createGlobalSeqNo(paramGlob).subscribe(datatt => {
            if (datatt.returnCode.code == 'success') {
                let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                commonHeader['globalSeqNo'] = datatt.globalSeqNo;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                this.doSub(item);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: datatt.returnCode.message });
                this.doSub(item);
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
            this.doSub(item);
        })
    }

    doSub(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                this.isCommitDis = false;
                let param = {
                    custRequestId: item.custRequestId,// 
                    operationType: '03',// 
                };

                this.httpService.saveOrUpdateCustRequest(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                        commonHeader['globalSeqNo'] = '';
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                        this.querySecle();
                        this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page2/workdesk-page'], { queryParams: { flag: '1', totalNum: '2' }});
                    } else {
                        this.isCommitDis = true;
                        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                        commonHeader['globalSeqNo'] = '';
                        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.isCommitDis = true;
                    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                    commonHeader['globalSeqNo'] = '';
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })

            }
        });
    }
    // 
    toDelete(item) {
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let param = {
                    custRequestId: item.custRequestId,// 
                    operationType: '04',// 
                };
                this.httpService.saveOrUpdateCustRequest(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.querySecle();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: "" });
                })
            }
        });
    }

    // 
    toVeto(item) {
        this.VetoData = [];
        this.VetoData.push(item);
        this.display = true;
        this.custRequestId = item.custRequestId;
    }

    //
    vetoClick() {
        if (this.firstVetoReason == '' || this.firstVetoReason == undefined || this.firstVetoReason == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        if (this.secondVetoReason == '' || this.secondVetoReason == undefined || this.secondVetoReason == null) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            return
        }
        // -
        if (this.secondVetoReason == '4110') {
            if (this.comments == '' || this.comments == undefined || this.comments == null) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                return
            }
        }

        if (this.VetoData[0].chanelType && (this.VetoData[0].chanelType == '2' || this.VetoData[0].chanelType == '1')) {

            let Vetoparam = {
                oneVetoReason: this.firstVetoReason,
                twoVetoReason: this.secondVetoReason,
                applyDate: this.VetoData[0]['applyDate'],
                voteId: this.userId,// 
                voteOrg: this.orgId,// 
                votePost: this.postNameVeto,//  
                voteReason: this.comments, // 
                custNo: this.VetoData[0]['custNo'],// 
                globalType: this.VetoData[0]['globalType'], // 
                globalId: this.VetoData[0]['globalId'],// 
                loanPurpuse: this.VetoData[0]['loanPurpuse'], // 
                taskType: this.VetoData[0]['taskType'], // 
                workEffortId: this.VetoData[0]['workEffortId'], // ID

                requestSeqNo: this.VetoData[0]['custRequestId'], // 
                dataSource: '02',  // 
                bussType: this.VetoData[0]['bussType'], // 
            }
            this.customCenterHttpService.voteApplicant(Vetoparam).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.display = false;
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '', detail: '!' }];
                    this.disposeSchedule();
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, error => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            })

        } else {
            let param = {
                custRequestId: this.custRequestId,// 
                operationType: '05',// 
                firstVetoReason: this.firstVetoReason,
                secondVetoReason: this.secondVetoReason,
                defeatRemark: this.comments,
                userId: this.userId,
            };
            this.httpService.saveOrUpdateCustRequest(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    // this.display = false;
                    this.close();
                    this.querySecle();
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, error => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            })
        }
    }

    //next
    disposeSchedule() {

        // 
        this.vetoFirsts.forEach(element => {
            if (element.value == this.firstVetoReason) {
                this.reasonOne = element.label;
            }
        });
        if (!this.bzIsshow) {// -
            this.vetoOption.forEach(element => {
                if (element.value == this.secondVetoReason) {
                    this.reasonTwo = element.label;
                }
            });
            this.subApproveMapBean.info = this.reasonOne + ',' + this.reasonTwo;
        } else {// -
            this.reasonTwo = this.comments;
            this.subApproveMapBean.info = this.reasonOne + ',' + this.comments;
        }

        this.bussinessMap.approve = '1';// 
        this.subApproveMapBean.result = '1';// 
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.approveMap = this.subApproveMapBean;
        this.appproResultBean.taskId = this.VetoData[0]['taskId'];

        this.customCenterHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.close();
                this.querySecle();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                this.close();
                this.querySecle();
            }
        }, error => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            this.close();
            this.querySecle();
        })
    }

    // 
    vetoChange(event) {
        this.comments = '';
        this.bzIsshow = false;
        if (event.value == '06') {
            this.vetoOption = this.personVeto;
        } else if (event.value == '07') {
            this.vetoOption = this.financeVeto;
        } else if (event.value == '08') {
            this.vetoOption = this.manageVeto;
        } else if (event.value == '09') {
            this.vetoOption = this.serveVeto;
        } else if (event.value == '10') {
            this.vetoOption = this.otherVeto;
        }
        if (event.value == '') {
            this.secondVetoReason = '';
            this.vetoOption = [];
        } else {
            this.secondVetoReason = this.vetoOption[0].value
        }
    }

    // 
    vetoChangeTwoReson(event) {
        this.comments = '';
        if (event.value == '4110') {
            this.bzIsshow = true;
        } else {
            this.bzIsshow = false;
        }
    }

    close() {
        this.display = false;
        this.vetoOption = [];
        this.firstVetoReason = '';
        this.secondVetoReason = '';
        this.comments = '';
        this.bzIsshow = false;
    }

    //
    onHideClose() {
        this.display = false;
        this.vetoOption = [];
        this.firstVetoReason = '';
        this.secondVetoReason = '';
        this.comments = '';
        this.bzIsshow = false;
    }

    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }

}